/**
 * 
 */
import React from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import { Route, Link } from 'react-router-dom'
import classnames from 'classnames'
import config from '../../../../utils/config'
import socket from '../../../../utils/socket'

import AllOrders from './subpage/All'
import WaitPay from './subpage/WaitPay'
import WaitSend from './subpage/WaitSend'
import WaitSign from './subpage/WaitSign'
import WaitComment from './subpage/WaitComment'


class Order extends React.Component {
  componentWillMount() {
    if (socket.status === 'open') {
      socket.ws.send(JSON.stringify({
        type: 'get_orders',
        content: { limit: 0 }
      }))
    }
  }
  render() {
    return (
      <div>
        <div className="u-order-wrap">
          <div className="u-o-item"><Link className={classnames({
            'uoi-hit': this.props.location.pathname !== '/member/myorder'
          })} to="/member/myorder"></Link></div>
          <div className="u-o-item"><Link className={classnames({
            'uoi-hit': this.props.location.pathname !== '/member/myorder/waitpay'
          })} to="/member/myorder/waitpay"> {this.props.myOrders.status.waitPay}</Link></div>
          <div className="u-o-item"><Link className={classnames({
            'uoi-hit': this.props.location.pathname !== '/member/myorder/waitsend'
          })} to="/member/myorder/waitsend"> {this.props.myOrders.status.waitSend}</Link></div>
          <div className="u-o-item"><Link className={classnames({
            'uoi-hit': this.props.location.pathname !== '/member/myorder/waitsign'
          })} to="/member/myorder/waitsign"> {this.props.myOrders.status.waitSign}</Link></div>
          <div className="u-o-item u-o-item-last"><Link className={classnames({
            'uoi-hit': this.props.location.pathname !== '/member/myorder/waitcomment'
          })} to="/member/myorder/waitcomment"> {this.props.myOrders.status.waitComment}</Link></div>
        </div>
        <div>
          <Route exact path="/member/myorder" render={() => <AllOrders orders={this.props.myOrders.orders} />} />
          <Route exact path="/member/myorder/waitpay" render={() => <WaitPay orders={this.props.myOrders.orders} />} />
          <Route exact path="/member/myorder/waitsend" render={() => <WaitSend orders={this.props.myOrders.orders} />} />
          <Route exact path="/member/myorder/waitsign" render={() => <WaitSign orders={this.props.myOrders.orders} />} />
          <Route exact path="/member/myorder/waitcomment" render={() => <WaitComment orders={this.props.myOrders.orders} />} />
        </div>
      </div>
    )
  }
}

Order.propTypes = {
  myOrders: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
  myOrders: state.order.myOrders || {status: {}, orders:[]}
})

export default connect(mapStateToProps)(Order)
