import React from 'react'
import config from '../../../../../utils/config'
import { formatDate } from '../../../../../utils/tools'

const HOST = config.HOST

const AllOrders = props => {
  return (
    <div className="o-a-wrap">
      {
        props.orders.map((item, index) => (
          <div className="order-item" key={index}>
            <div className="order-head">
              <div className="oi-date">{formatDate(item.creaTime, true)}</div>
              <div className="oi-no">{item.orderno}</div>
              <div className="oi-date">{item.sumPrice}</div>
            </div>
            <div>
              {
                item.products.map((product, i) => (
                  <div key={i}>
                    <div><img width="80px" src={`${'http://118.126.108.36'}/image/goods/logo/${product.logo}_210x210q90.jpg`} alt={product.goodName}/></div>
                    <div>
                      <div>{product.goodName}</div>
                      <div>{product.price}</div>
                      <div>{product.number}</div>
                    </div>
                  </div>
                ))
              }
            </div>
          </div>
        ))
      }
    </div>
  )
}

export default AllOrders
